import Vue from 'vue';
import Vuex from 'vuex';

    //   
import __main__ from './modules/__main__';
import productCatModule from './modules/product_cat';

Vue.use(Vuex);

const store = new Vuex.Store({
        //  state, getters  .   
    modules: {
        __main__,
        product_cat: productCatModule,
    }
});

export default store;



