	//   
const state = {
    CountPay: ''
};

	//      
const mutations = {
  	set(state, { type, items }) {
  	  	state[type] = items;
  	}
};

    //       
const getters = {
 	CountPay: (state, getters) => state.CountPay
};

export default {
    state,
    getters,
    mutations
};


