	/**
	 *    
	 */

export default {
	methods: {
		$_mixinNotification_AddBasket () {
			this.$notify({
			  	group: 'foo',
			  	title: '  ',
			  	text: '    ',
			  	position: 'top right',
			});
		},

		$_mixinNotification_ChangeRating () {
			this.$notify({
			  	group: 'foo',
			  	title: '!',
			  	text: '  ',
			  	position: 'top right',
			});
		},
	}
}


